@import "../../styles/variables.scss";

.pagination-container {
  display: flex;
  align-items: center;
  position: relative;
  .pagination-text {
    margin: 5px;
  }
  .navigation-button {
    border-radius: 25px;
    width: 25px;
    height: 25px;
    border: none;
    background-color: $background-color-ternary;
  }
  .navigation-button:hover {
    i {
      font-weight: 900 !important;
    }
  }
  .custom-dropdown {
    width: auto;
    margin-bottom: 0px;
    margin-left: 10px;
    height: auto;
    padding: 5px 10px;
  }
  .matched-records-data {
    position: absolute;
    top: 35px;
    left: 5px;
  }
}
